'use client';

import { Button, Link, Dropdown, Label, useIsHydrated } from "@heroui/react";
import { useUserStore } from "../../stores/user/userStore";
import { useState } from "react";
import NextLink from "next/link";
import UserFilter from "../../features/user/UserFilter";
import UserTable from "@/features/user/UserTable";


export default function UsersPage() {
    const hydration = useIsHydrated();

    const {
        searchQuery,
        roleFilter,
        statusFilter,
        setSearchQuery,
        setRoleFilter,
        setStatusFilter,
        deleteUser,
        getFilteredUsers
    } = useUserStore();


    const [deleteModal, setDeleteModal] = useState<{ isOpen: boolean; userId: string; userName: string }>({
        isOpen: false,
        userId: '',
        userName: ''
    });

    const handleDelete = (id: string, name: string) => {
        setDeleteModal({ isOpen: true, userId: id, userName: name });
    };

    const confirmDelete = () => {
        deleteUser(deleteModal.userId);
        setDeleteModal({ isOpen: false, userId: '', userName: '' });
    };

    const cancelDelete = () => {
        setDeleteModal({ isOpen: false, userId: '', userName: '' });
    };

    if (!hydration) {
        return null;
    }

    return (
        <div className="min-h-screen bg-black text-white p-6">
            {/* Header */}
            <div className="mb-8">
                <h1 className="text-2xl font-bold mb-1">User Management</h1>
                <p className="text-gray-400 text-sm">Manage system users and permissions</p>
            </div>

            {/* Filters and Actions */}
            <UserFilter />

            {/* Table */}
            <UserTable />

            {/* Pagination */}


            {/* Delete Confirmation Modal */}
            {deleteModal.isOpen && (
                <div className="fixed inset-0 bg-black/85 flex items-center justify-center z-50" style={{ position: 'fixed', top: 0, left: 0, right: 0, bottom: 0 }}>
                    <div className="bg-zinc-900 rounded-lg p-6 max-w-sm w-full mx-4 shadow-2xl border border-zinc-700">
                        <h3 className="text-lg font-bold text-white mb-3">Confirm Deletion</h3>

                        <p className="text-gray-300 text-sm mb-6">
                            Are you sure you want to delete <strong className="text-white">{deleteModal.userName}</strong>?<br />
                            This action cannot be undone.
                        </p>

                        <div className="flex gap-3 justify-end">
                            <Button
                                onClick={cancelDelete}
                                className="bg-zinc-800 hover:bg-zinc-700 text-white px-6"
                            >
                                Cancel
                            </Button>
                            <Button
                                onClick={confirmDelete}
                                className="bg-red-600 hover:bg-red-700 text-white px-6"
                            >
                                Delete
                            </Button>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
}